import { ModuleWithProviders } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { ElementAdjustmentModule } from "./element-adjustment.module";
import { ElementAdjustment } from "./element-adjustment.component";


const routes: Routes = [
    {
        path: '',
        component: ElementAdjustment,
        children: [
            { path: 'account-integral', loadChildren: './account-integral/account-integral.module#AccountIntegralModule' },//用户积分调整
            { path: 'consume-overdraw-quota', loadChildren: './consume-overdraw-quota/consume-overdraw-quota.module#ConsumeOverDrawQuotaModule' },//消费额度调整
            { path: 'consumption-limit', loadChildren: './consumption-limit/consumption-limit.module#ConsumptionLimitModule' },//消费额度调整
            { path: 'credit-card-savings-adjustment', loadChildren: './credit-card-savings-adjustment/credit-card-savings-adjustment.module#CreditCardSavingsAdjustmentModule' },//信用卡储蓄员号调整
            { path: 'card-status', loadChildren: './card-status/card-status.module#CardStatusModule' },//卡状态调整
            { path: 'card-elements', loadChildren: './card-elements/card-elements.module#CardElementsModule' },//卡要素调整
            { path: 'guarantee-method', loadChildren: './guarantee-method/guarantee-method.module#GuaranteeMethodModule' },//卡要素调整
            { path: 'out-card-verify', loadChildren: './out-card-verify/out-card-verify.module#OutCardVerifyModule' },//出卡核实
        ]
    }
];
export const ElementAdjustmentRouting: ModuleWithProviders = RouterModule.forChild(routes);